<template>
  <div>
    <div class="zzBox row center" v-if="is_show" @click="showActive()">
      <div class="ctr" >
          <img :src="require('@/assets/st-imges/' + name+'.png')" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['is_show','name'],
  data() {
    return {
      show:false,
      
    };
  },
  methods:{
    showActive(){
      this.is_show = false
     
    }
  },
  computed:{},
  mounted(){

   
  },  
};
</script>
<style scoped>
.zzBox {
    position: fixed;
  z-index: 100001;
  top: 0;
  background-color: #000000;
  opacity: 0.8;
  width: 100%;
  height: 100vh;
}
.ctr {

}

</style>


