<template>
  <div>
    <hader/>
    <div>
      <div class="banner">
        <!-- <div class="tit">[ 让每一个商户都成为我们的朋友 ]</div>
                <div class="cont">
                    <span>网址：www.dabojituan.com </span>
                    <span> 邮箱：dbzhpt@gmail.com</span>
                    <div>接待时间：7x24 小时</div>
        </div>-->
      </div>
      <div class="productCont">
        <div class="tit">产品介绍</div>
        <div class="list" @click="btn('sports')">
          <div class="imgBox sports">
            <div class="nm">体育游戏</div>
            <div>Sports games</div>
          </div>
          <div class="imgBox electron" @click.stop="btn('electron')">
            <div class="nm">电子游艺</div>
            <div>Electronics games</div>
          </div>
          <div class="imgBox fishing" @click.stop="btn('fishing')">
            <div class="nm">捕鱼游戏</div>
            <div>Fishing games</div>
          </div>
          <div class="imgBox immortal" @click.stop="btn('immortal')">
            <div class="nm">真人视讯</div>
            <div>Live Video</div>
          </div>
          <div class="imgBox lottery" @click.stop="btn('lottery')">
            <div class="nm">彩票游戏</div>
            <div>Lottery games</div>
          </div>
          <div class="imgBox board" @click.stop="btn('board')">
            <div class="nm">棋牌游戏</div>
            <div>Board games</div>
          </div>
        </div>
      </div>
           <!--  -->
      <div class="listimg">
        <div class="listimgbox row" v-for="(item,index) in imgList.list" :key="index">
          <div class="row center"  @click.stop="btn(item.one,1)">
            <img :src="item.one">
          </div>
          <div class="row center" @click.stop="btn(item.two,1)">
            <img :src="item.two">
          </div>
          <div class="row center" @click.stop="btn(item.three,1)">
            <img :src="item.three">
          </div>
        </div>
      </div>
    </div>
    <div>
    </div>
    <div>
        <div class="titlelist row center" v-for="(item,index) in imgList.title" :key="index">
            <img :src="item.path">
        </div>
    </div>
    <footera/>
    <div class="nav row average">
      <div :class="{active:nav == 'index'}" @click="navClick('index')">
        <div>
          <img :src="nav == 'index'?img.indexa:img.index">
        </div>
        <div>
          <p>首页</p>
        </div>
      </div>
      <div class="column average" :class="{active:nav == 'product'}" @click="navClick('product')">
        <div>
          <img :src="nav == 'product'?img.producta:img.product">
        </div>
        <div>
          <p>产品</p>
        </div>
      </div>
      <div
        class="column average"
        :class="{active:nav == 'advantage'}"
        @click="navClick('advantage')"
      >
        <div>
          <img :src="nav == 'advantage'?img.advantagea:img.advantage">
        </div>
        <div>
          <p>优势</p>
        </div>
      </div>
      <div class="column average" :class="{active:nav == 'partner'}" @click="navClick('partner')">
        <div>
          <img :src="nav == 'partner'?img.partnera:img.partner">
        </div>
        <div>
          <p>伙伴</p>
        </div>
      </div>
      <div
        class="column average"
        :class="{active:nav == 'customized'}"
        @click="navClick('customized')"
      >
        <div>
          <img :src="nav == 'customized'?img.creationa:img.creation">
        </div>
        <div>
          <p>定制</p>
        </div>
      </div>
      <div class="column average" :class="{active:nav == 'about'}" @click="navClick('about')">
        <div>
          <img :src="nav == 'about'?img.abouta:img.about">
        </div>
        <div>
          <p>关于</p>
        </div>
      </div>
    </div>

     <div class="zzBox row center" v-if="show" @click="showActive()">
      <div class="ctr" >
          <img :src="loading" :style="{maxWidth:'90%',margin:'5%',marginTop:'2%' }">
      </div>
    </div>
  </div>
</template>
<script>
import hader from "@/components/header";
import footera from "@/components/footer";
import loading from "@/components/loading";
export default {
  name: "product",
  components: {
    hader,
    footera,
    loading
  },
  data() {
    return {
      loading:'',
      show:false,
      list: [
        {
          title: "体育游戏",
          cont: "Sports games"
        },
        {
          title: "电子游艺",
          cont: "Electronics games"
        },
        {
          title: "捕鱼游戏",
          cont: "fishing"
        },
        {
          title: "真人视讯",
          cont: "Live Video"
        },
        {
          title: "彩票游戏",
          cont: "lottery game"
        }
      ],
      name: "",
      show: false,
      data: "",
      nav: "product",
      img: {
        index: require("@/assets/st-imges/index.png"),
        indexa: require("@/assets/st-imges/indexa.png"),
        about: require("@/assets/st-imges/about.png"),
        abouta: require("@/assets/st-imges/abouta.png"),
        creation: require("@/assets/st-imges/creation.png"),
        creationa: require("@/assets/st-imges/creationa.png"),
        advantage: require("@/assets/st-imges/advantage.png"),
        advantagea: require("@/assets/st-imges/advantagea.png"),
        partner: require("@/assets/st-imges/partner.png"),
        partnera: require("@/assets/st-imges/partnera.png"),
        product: require("@/assets/st-imges/product.png"),
        producta: require("@/assets/st-imges/producta.png")
      },
      imgList: {
        list: [
          {
            one: require("@/assets/st-imges/product/1.jpg"),
            two: require("@/assets/st-imges/product/2.jpg"),
            three: require("@/assets/st-imges/product/3.jpg")
          },
          {
            one: require("@/assets/st-imges/product/4.jpg"),
            two: require("@/assets/st-imges/product/5.jpg"),
            three: require("@/assets/st-imges/product/26.jpg")
          },
          {
            one: require("@/assets/st-imges/product/6.jpg"),
            two: require("@/assets/st-imges/product/7.jpg"),
            three: require("@/assets/st-imges/product/8.jpg")
          },
          {
            one: require("@/assets/st-imges/product/9.jpg"),
            two: require("@/assets/st-imges/product/10.jpg"),
            three: require("@/assets/st-imges/product/11.jpg")
          },
          {
            one: require("@/assets/st-imges/product/12.jpg"),
            two: require("@/assets/st-imges/product/13.jpg"),
            three: require("@/assets/st-imges/product/14.jpg")
          },
          {
            one: require("@/assets/st-imges/product/15.jpg"),
            two: require("@/assets/st-imges/product/16.jpg"),
            three: require("@/assets/st-imges/product/17.jpg")
          },
          {
            one: require("@/assets/st-imges/product/18.jpg"),
            two: require("@/assets/st-imges/product/19.jpg"),
            three: require("@/assets/st-imges/product/20.jpg")
          }
        ],
        title: [
            {path:require("@/assets/st-imges/productA1.png")},
            {path:require("@/assets/st-imges/productB.png")},
            {path:require("@/assets/st-imges/productC.png")},
            {path:require("@/assets/st-imges/productD.png")},
            {path:require("@/assets/st-imges/productE.png")},
            {path:require("@/assets/st-imges/productF.png")},
            {path:require("@/assets/st-imges/productG.png")},
            {path:require("@/assets/st-imges/productH.png")},
            ]
      }
    };
  },
  methods: {
    navClick(str) {
      // this.nav = str
      if (str == this.nav) return false;
      let path = str;
      if (str == "index") {
        path = "/";
      }
      this.$router.push({ path: path });
    },
    btn(e,type) {
      this.imgsrc=this.name = e;
    
      this.pictype='.png'
       if(type == 1){
        let str = e.split('/');
        let name = str[3].split('.')
        this.name = name[0]
        this.pictype='.jpg'
        this.imgsrc='product/'+this.name;
        this.loading = require('@/assets/st-imges/' + this.imgsrc+this.pictype)
        this.show = true;
      }
      // this.loading = require('@/assets/st-imges/' + this.imgsrc+this.pictype)
      // this.show = true;
    },
     showActive(){
      this.show = false
     
    }
  }
};
</script>
<style scoped>
.banner {
  background: url("./../../assets/st-imges/productBanner.png") no-repeat;
  background-size: 100% 100%;
  background-color: rgba(20, 10, 3, 1);
  /* height: 1.3rem; */
  color: #fff;
  height: 2.3rem;
  padding: 0.54rem 0.5rem 0.64rem;
  margin-top: 1.28rem;
}
.showimg{
  margin:0 5% 0 5%;
  width: 90%;
}
.banner .tit {
  text-align: center;
  font-size: 0.36rem;
  margin-bottom: 0.12rem;
}
.banner .cont {
  text-align: center;
  font-size: 0.26rem;
  line-height: 0.4rem;
}
.productCont {
  margin: 0.4rem 0.2rem 0;
  text-align: center;
}
.productCont .tit {
  margin: 0.4rem 0 0.44rem;
  position: relative;
  font-size: 0.34rem;
  color: #555555;
  font-weight: bold;
}
.productCont .tit::after {
  display: inline-block;
  content: "";
  width: 0.6rem;
  height: 0.02rem;
  background: #e63440;
  position: absolute;
  bottom: -0.1rem;
  left: 46%;
}
.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
/* 导航 */
.nav {
  position: fixed;
  bottom: 0;
  left: 0;
  background: #fff;
  height: 1rem;
  width: 100%;
  padding: 0.1rem 0;
  border-top: 1px solid #dddddd;
}
.nav > div {
  width: 16%;
  height: 100%;
}
.nav > div > div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.nav > div img {
  width: 0.34rem;
  height: 0.34rem;
}
.active {
  color: #e63440;
}
/* 产品图片背景 */
.imgBox {
  width: 2.2rem;
  height: 2.8rem;
  margin-bottom: 0.25rem;
  /* margin: 0 0.25rem 0.25rem 0; */
  padding-top: 1.72rem;
}
.imgBox:nth-child(3n),
.imgBox:nth-last-child(1) {
  margin: 0 0 0.25rem 0;
}
.sports {
  background: url("./../../assets/st-imges/sports.png") no-repeat center center;
  background-size: 100% 100%;
}
.fishing {
  background: url("./../../assets/st-imges/fishing.png") no-repeat center center;
  background-size: 100% 100%;
}
.electron {
  background: url("./../../assets/st-imges/electron.png") no-repeat center
    center;
  background-size: 100% 100%;
}
.lottery {
  background: url("./../../assets/st-imges/lottery.png") no-repeat center center;
  background-size: 100% 100%;
}
.immortal {
  background: url("./../../assets/st-imges/immortal.png") no-repeat center
    center;
  background-size: 100% 100%;
}
.more {
  background: url("./../../assets/st-imges/more.png") no-repeat center center;
  background-size: 100% 100%;
}
.board {
  background: url("./../../assets/st-imges/board.png") no-repeat center center;
  background-size: 100% 100%;
}
.imgBox div {
  color: #e63440;
  font-size: 0.2rem;
}
.imgBox .nm {
  font-size: 0.32rem;
  font-weight: bold;
}
.listimg {
  height: 32.7rem;
}
.listimgbox {
  height: 4.38rem;
    margin-top: 0.3rem;
}
.listimgbox div {
  width: 33%;
  height: 4.38rem;

}
.listimgbox div img {
  width: 2.2rem;
  height: 4.38rem;
}
.titlelist{
  margin-bottom: .3rem
}
.titlelist img{
  width: 6.91rem;
  height: 4rem;
}
.zzBox {
    position: fixed;
  z-index: 100001;
  top: 0;
  background-color: #000000;
  /* opacity: 0.8; */
  width: 100%;
  height: 100vh;
}
</style>



